import React, { useCallback, useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { useForm } from 'react-hook-form';

import {
  Row,
  Column,
  SelectCustom,
  LabelCustom,
  Text,
  RowPanel,
  LeftPanel,
  CenterPanel,
  RightPanel,
  Button as ButtonCustom,
  InputCustom
} from '../../components';

import {
  Container,
  WrapperTop,
  WrapperImport,
  IconCsv,
  WrapperTitleQuotes,
  WrapperExport,
  IconPrint,
  Panel,
  IconHand,
  WrapperIcons,
  Title,
  Button,
  TagIcon,
  ArrowUpIcon,
  ArrowDownIcon,
  WrapperRowPanel,
  Import,
  IconSave,
  WrapperSave,
  WrapperPanel,
  WrapperInputs
} from './styles';

interface ParamTypes {
  quoteId: string;
}

interface IQuote {
  id: string;
  cod: string;
  amount: number;
  product: string;
}

const mocaQuotesItens = [
  { id: '0001', cod: '123', amount: 10, product: 'placa teste 1' },
  { id: '0002', cod: '456', amount: 5, product: 'placa teste 2' }
];

interface IShipping {
  id: string;
  type: string;
}
const mocaShipping = [
  { id: '0001', type: 'tipo 1' },
  { id: '0002', type: 'tipo 2' }
];

interface IPaymentConditions {
  id: string;
  condition: string;
}
const mocaPaymentConditions = [
  { id: '0001', condition: 'à vista' },
  { id: '0002', condition: '30 dias' },
  { id: '0003', condition: '60 dias' }
];

interface IHeaderInfo {
  Buyer: string;
  IssuanceDate: string;
  Deadline: string;
  ZipCode: string;
  Address: string;
  Number: string;
  Complement?: string;
  Neighborhood: string;
  State: string;
  City: string;
}
const mocaHeaderInfo = {
  Buyer: 'Lakkatos',
  IssuanceDate: '22/03/2020',
  Deadline: '22/03/2020',
  ZipCode: '12345-00',
  Address: 'avenida teste',
  Number: '12345',
  Complement: 'torre 3',
  Neighborhood: 'Vila Teste',
  State: 'SP',
  City: 'São Paulo'
};

interface ActiveQuoteItem {
  Index: number;
}

const QuotesDetail: React.FC = () => {
  const { quoteId } = useParams<ParamTypes>();
  const [quote, setQuote] = useState<IQuote[]>([]);
  const [shipping, setShipping] = useState<IShipping[]>([]);
  const [paymentConditions, setPaymentConditions] = useState<
    IPaymentConditions[]
  >([]);
  const [headerInfo, setHeaderInfo] = useState<IHeaderInfo>();
  const [quoteItem, setQuoteItem] = useState<ActiveQuoteItem>({
    Index: -1
  });
  const { register } = useForm();

  const handlerCollapse = useCallback(index => {
    setQuoteItem(prevState => ({
      ...prevState,
      Index: prevState.Index === index ? -1 : index
    }));
  }, []);

  useEffect(() => {
    setHeaderInfo(mocaHeaderInfo);
    setQuote(mocaQuotesItens);
    setShipping(mocaShipping);
    setPaymentConditions(mocaPaymentConditions);
  }, []);

  return (
    <Container>
      <WrapperTop>
        <WrapperImport>
          <span>Importar</span>
          <Import>
            <input id="file-upload" type="file" required />
            <label htmlFor="file-upload">
              <IconCsv />
            </label>
            <WrapperSave className="import">
              <IconSave />
              <span>Salvar</span>
            </WrapperSave>
          </Import>
        </WrapperImport>
        <WrapperTitleQuotes>
          <IconHand />
          <Title>cotação</Title>
          <span>{quoteId}</span>
        </WrapperTitleQuotes>
        <WrapperExport>
          <span>Exportar</span>
          <WrapperIcons>
            <Button>
              <IconCsv />
            </Button>
            <Button>
              <IconPrint />
            </Button>
          </WrapperIcons>
        </WrapperExport>
      </WrapperTop>
      <Panel>
        <Row>
          <Column>
            <LabelCustom>Comprador</LabelCustom>
            <Text>{headerInfo?.Buyer}</Text>
          </Column>
        </Row>
        <Row>
          <Column>
            <LabelCustom>Data Emissão</LabelCustom>
            <Text>{headerInfo?.IssuanceDate}</Text>
          </Column>
          <Column>
            <LabelCustom>Data Limite</LabelCustom>
            <Text>{headerInfo?.Deadline}</Text>
          </Column>
        </Row>
        <Row>
          <Column>
            <LabelCustom>CEP</LabelCustom>
            <Text>{headerInfo?.ZipCode}</Text>
          </Column>
        </Row>
        <Row>
          <Column>
            <LabelCustom>Endereço</LabelCustom>
            <Text>{headerInfo?.Address}</Text>
          </Column>
          <Column>
            <LabelCustom>Numero</LabelCustom>
            <Text>{headerInfo?.Number}</Text>
          </Column>
        </Row>
        <Row>
          <Column>
            <LabelCustom>Complemento</LabelCustom>
            <Text>{headerInfo?.Complement}</Text>
          </Column>
          <Column>
            <LabelCustom>Bairro</LabelCustom>
            <Text>{headerInfo?.Neighborhood}</Text>
          </Column>
        </Row>
        <Row />
        <Row>
          <Column>
            <LabelCustom>UF</LabelCustom>
            <Text>{headerInfo?.State}</Text>
          </Column>
          <Column>
            <LabelCustom>Cidade</LabelCustom>
            <Text>{headerInfo?.City}</Text>
          </Column>
        </Row>
        <Row>
          <Column>
            <LabelCustom htmlFor="shipping">Tipo Frete</LabelCustom>
            <SelectCustom id="shipping" name="shipping">
              <option key="-1"> Selecione </option>
              {shipping.map(({ id, type }) => (
                <option key={id}> {type}</option>
              ))}
            </SelectCustom>
          </Column>
        </Row>
        <Row>
          <Column>
            <LabelCustom htmlFor="shipping">Cond. Pagamento</LabelCustom>

            <SelectCustom id="paymentConditions" name="paymentConditions">
              <option key="-1"> Selecione </option>
              {paymentConditions.map(({ id, condition }) => (
                <option key={id}> {condition}</option>
              ))}
            </SelectCustom>
          </Column>
        </Row>
        <Row>
          <Column>
            <ButtonCustom outlined>Salvar</ButtonCustom>
          </Column>
        </Row>
      </Panel>

      <WrapperPanel>
        {quote.map(({ id, cod, amount, product }, index) => (
          <WrapperRowPanel key={id}>
            <RowPanel onClick={() => handlerCollapse(index)}>
              <LeftPanel>
                <TagIcon />
              </LeftPanel>
              <CenterPanel>
                <Row>
                  <Column>
                    <LabelCustom>Item</LabelCustom>
                    <Text>{id}</Text>
                  </Column>
                  <Column>
                    <LabelCustom>Cod</LabelCustom>
                    <Text>{cod}</Text>
                  </Column>
                </Row>
                <Row>
                  <Column>
                    <LabelCustom>Produto</LabelCustom>
                    <Text colorFont="blue-light">{product}</Text>
                  </Column>
                  <Column>
                    <LabelCustom>Quantidade</LabelCustom>
                    <Text>{amount}</Text>
                  </Column>
                </Row>
              </CenterPanel>
              <RightPanel>
                <ArrowUpIcon
                  className={quoteItem.Index === index ? 'active' : ''}
                />
                <ArrowDownIcon
                  className={quoteItem.Index === index ? '' : 'active'}
                />
              </RightPanel>
            </RowPanel>
            <WrapperInputs
              className={quoteItem.Index === index ? 'active' : ''}
            >
              <Panel>
                <Row>
                  <Column>
                    <LabelCustom>Cod. Produto</LabelCustom>
                    <InputCustom placeholder="Digite o Código" type="text" />
                  </Column>
                </Row>
                <Row>
                  <Column>
                    <LabelCustom>Valor Unitário</LabelCustom>
                    <InputCustom placeholder="Digite o Valor" type="text" />
                  </Column>
                </Row>
                <Row>
                  <Column>
                    <LabelCustom>Aliq. IPI(%)</LabelCustom>
                    <InputCustom placeholder="Digite o IPI" type="text" />
                  </Column>
                </Row>

                <Row>
                  <Column>
                    <LabelCustom>Aliq. ICMS(%)</LabelCustom>
                    <InputCustom placeholder="Digite o ICMS" type="text" />
                  </Column>
                </Row>
                <Row>
                  <Column>
                    <LabelCustom>Prazo Entrega(Dias)</LabelCustom>
                    <InputCustom placeholder="Digite o(s) dia(s)" type="text" />
                  </Column>
                </Row>
                <Row>
                  <Column>
                    <LabelCustom>Descrição</LabelCustom>
                    <InputCustom placeholder="Digite a descrição" type="text" />
                  </Column>
                </Row>
                <Row>
                  <Column>
                    <LabelCustom>Observação (Opcional)</LabelCustom>
                    <InputCustom
                      placeholder="Digite uma Observação"
                      type="text"
                    />
                  </Column>
                </Row>
                <Row>
                  <Column>
                    <ButtonCustom outlined>Salvar</ButtonCustom>
                  </Column>
                </Row>
              </Panel>
            </WrapperInputs>
          </WrapperRowPanel>
        ))}
      </WrapperPanel>
    </Container>
  );
};
export default QuotesDetail;
